	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"OVERVIEW","depth":2},{"value":"USER NEEDS","depth":2},{"value":"SOLUTION","depth":2}],"html":"<h2>OVERVIEW</h2>\n<p>Redesigning iOS is a unique challenge for an individual to pursue as it is a multifaceted system developed by hundreds of people with different roles. For this project I set out to conceptualize and mockup the various additions to the operations system I believe are essential to a redesign such as implementation of Augmented Reality, customization and personalization, and general improvements and new features.</p>\n<h2>USER NEEDS</h2>\n<p>Everyone uses an iPhone differently, every user has different apps, needs, and ways they use and interact with their digital content on iOS devices. My goal for my iOS redesign is to address the lack of personalization and give users the ability to customize their phone as they see fit. Next, as I believe Augmented Reality is a technology that will be fundamental to the future of digital interaction, and may one day replace the smartphone entirely, I wanted to incorporate AR into the core of the iOS experience while being intuitive and functional. As a result, this redesign will feature Augmented Reality working together with a constantly learning Siri at the core of the operating system, allowing for new innovative ways to use an iPhone.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 88.27777777777777%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/the-next-website/static/perspective-691ca37047b208397fa8696c458c65e3-53128.png\"\n        srcset=\"/the-next-website/static/perspective-691ca37047b208397fa8696c458c65e3-7266b.png 375w,\n/the-next-website/static/perspective-691ca37047b208397fa8696c458c65e3-0a3c1.png 750w,\n/the-next-website/static/perspective-691ca37047b208397fa8696c458c65e3-53128.png 1500w,\n/the-next-website/static/perspective-691ca37047b208397fa8696c458c65e3-f4c53.png 1800w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>SOLUTION</h2>\n<p>The main improvements I am proposing in my redesign of iOS are; Augmented Reality and Siri implemented at the core of the device, a redesigned notification system, new ways to personalize your device, and general improvements and new features.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 64.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"null\"\n        title=\"\"\n        src=\"/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-35578.jpg\"\n        srcset=\"/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-21994.jpg 375w,\n/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-f7c9d.jpg 750w,\n/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-35578.jpg 1500w,\n/the-next-website/static/calcada1-ff26a71c6de3a5ee4660b79f8cb3d93f-5ea4e.jpg 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>View this entire project by visiting <a href=\"http://michaelcalcada.com/iosredesign.html\">my website</a> or view the product page and video <a href=\"http://www.michaelcalcada.com/iosar/\">here</a>.</p>","frontmatter":{"title":"iOS AR: A complete redesign of iOS","graduate":"Michael Calcada","category":"UI/UX","category2":"Product Design","category3":"Motion Design","image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5,"src":"/the-next-website/static/iosarcard-6309e2ab92b4a12b623e2d5c5f38fa70-9c75d.png","srcSet":"/the-next-website/static/iosarcard-6309e2ab92b4a12b623e2d5c5f38fa70-5cf12.png 375w,\n/the-next-website/static/iosarcard-6309e2ab92b4a12b623e2d5c5f38fa70-1040f.png 750w,\n/the-next-website/static/iosarcard-6309e2ab92b4a12b623e2d5c5f38fa70-9c75d.png 1280w","sizes":"(max-width: 1280px) 100vw, 1280px"}}}}}},"pathContext":{"slug":"/work/ios-ar-a-complete-redesign-of-ios/","name":"/iOS AR: A complete redesign of iOS/"}}

